.switch {
    margin-top: 20px;
    position: relative;
    display: inline-block;
    width: 60px;
    height: 20px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;

}

.slider:before {
    position: absolute;
    content: "";
    height: 12px;
    width: 40%;
    left: 5px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
}

input:checked + .slider {
    background-color: grey
}
input:checked + .slider:before {
    transform: translateX(26px);
}